import * as Yup from "yup";
import axios from "../../../axios";
import React, { Component } from "react";
import { Formik, Form, Field } from "formik";
import { Button, LinearProgress } from "@material-ui/core";
import { TextField } from "formik-material-ui";
import { connect } from "react-redux";
const mapStateToProps = (state) => ({
  username: state.username,
  id: state.id
});

class PasswordChange extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div class="container">
        <div class="row">
          <div class="col-sm">
            <div class="card">
              <div class="card-body">
                <h5 class="card-title">Change Login Password</h5>
                <Formik
                  initialValues={{
                    username: this.props.username,
                    id:this.props.id,
                    newPassword: "",
                    confirmPassword: "",
                  }}
                  validationSchema={Yup.object({
                    newPassword: Yup.string().required("Password is required"),
                    confirmPassword: Yup.string().oneOf(
                      [Yup.ref("newPassword"), null],
                      "Passwords must match"
                    ),
                  })}
                  onSubmit={(values, { setSubmitting, resetForm }) => {
                    setTimeout(() => {
                      setSubmitting(false);
                      axios
                        .post("login/Passwordchange", JSON.stringify(values), {
                          headers: {
                            // Overwrite Axios's automatically set Content-Type
                            "Content-Type": "application/json",
                            Accept: "application/json",
                          },
                        })
                        .then((res) => {
                          if (
                            res.status === 200 &&
                            res.data.error === "invalid Password"
                          ) {
                            alert("Entered old password is invalid!");
                          } else if (
                            res.status === 200 &&
                            res.data.flag === "Success"
                          ) {
                            alert("Password Successfully changed!");
                          }
                        })
                        .catch(function (error) {
                          alert("Error occured:" + error);
                        });
                      resetForm();
                    }, 500);
                  }}
                >
                  {({ submitForm, isSubmitting, values }) => (
                    <Form>
                      <div class="formspacer " />
                      <div class="row justify-content-start">
                        <div class="col">
                          <Field
                            component={TextField}
                            name="newPassword"
                            type="password"
                            label="New Password "
                            variant="outlined"
                            fullWidth
                          />
                          <br />
                          <br />
                          <Field
                            component={TextField}
                            name="confirmPassword"
                            type="password"
                            label="Confirm Password "
                            variant="outlined"
                            fullWidth
                          />
                        </div>
                      </div>

                      <br />
                      <br />
                      {isSubmitting && <LinearProgress />}
                      <Button
                        variant="contained"
                        color="primary"
                        disabled={isSubmitting}
                        onClick={submitForm}
                      >
                        Submit
                      </Button>
                    </Form>
                  )}
                </Formik>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(mapStateToProps)(PasswordChange);
